.app {
  font-family: Avenir;
}

.connect-wallet-overlay {
  position: fixed;
  z-index: 20;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, .9);
}

.connect-wrapper {
  position: absolute;
  width: 480px;
  left: 50%;
  top: 50%;
  padding: 20px 40px 40px;
  box-sizing: border-box;
  border-radius: 8px;
  transform: translate(-50%, -70%);
  background: #111;
}

.connect-title {
  font-size: 18px;
  font-weight: bold;
  color: #fff;
  margin-bottom: 20px;
}

.wallet-item {
  padding: 12px 20px;
  border: 1px solid #666;
  border-radius: 4px;
  color: #fff;
  cursor: pointer;
  background: #111 url('./assets/cyano.svg') 350px center / auto 24px no-repeat;
}

.wallet-item:hover {
  border-color: green;
}

.info-modal-overlay {
  position: fixed;
  z-index: 21;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: black;
}

.modal-wrapper {
  position: absolute;
  width: 640px;
  left: 50%;
  top: 50%;
  padding: 20px 40px 40px;
  box-sizing: border-box;
  border-radius: 8px;
  transform: translate(-50%, -80%);
  background: #111;
}

.close-btn {
  position: absolute;
  right: 12px;
  top: 12px;
  width: 18px;
  height: 18px;
  background: url('./assets/close.png') center center / 100% auto no-repeat;
}

.modal-icon {
  height: 64px;
  background: url('./assets/success.png') center center / auto 32px no-repeat;
}

.modal-icon-error {
  background-image: url('./assets/info_alert.png');
}

.modal-icon-loading {
  background-image: url('./assets/loading.png');
  animation: loading 2s linear infinite;
}

@-webkit-keyframes loading {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes loading {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}

.modal-text {
  padding: 8px 0;
  text-align: center;
  color: white;
}

.modal-text-success {
  color: green;
}

.modal-text-info {
  color: rgb(23, 124, 255);
}

.modal-text-error {
  color: red;
}

.modal-extra-text {
  text-align: center;
  font-size: 12px;
  color: white;
}

.transaction-link {
  color: #fff;
}

.intro-line {
  color: white;
}

.intro-line a {
  color: white;
}

/* header */

.header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 10;
  height: 64px;
  box-shadow: 0 0 8px rgba(0, 0, 0, .07);
  background: #fff;
}

.content-wrapper {
  width: 960px;
  margin: 0 auto;
}

.logo-wrapper {
  position: relative;
  float: left;
  width: 160px;
  height: 18px;
  margin-top: 23px;
  background: url('./assets/logo.png') left center / auto 18px no-repeat;
}

.logo-wrapper .site-intro {
  position: absolute;
  right: -10px;
  top: -8px;
  width: 20px;
  height: 20px;
  cursor: pointer;
  background: url('./assets/question.png') center center / 100% auto no-repeat;
}

.token-select {
  width: 140px;
  height: 52px;
}

.token-select > div[class*="-control"] {
  border-top: 1px solid transparent;
  border-left: none;
  border-right: none;
  border-bottom: 1px solid #2c2c2c;
  box-shadow: none;
}

.token-select > div[class*="-control"] > div:first-child {
  padding: 16px 6px;
}

.token-select div[class*="-ValueContainer"] {
  padding: 16px 6px;
}

.token-select span[class*="-indicatorSeparator"] {
  display: none;
}

.profile-wrapper {
  float: right;
  width: 240px;
  padding: 0 12px;
  box-sizing: border-box;
}

.account-address {
  display: inline-block;
  width: 120px;
  vertical-align: top;
  line-height: 64px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.signout-btn {
  float: right;
  cursor: pointer;
  padding: 4px 10px;
  margin-top: 16px;
  border: 1px solid red;
  border-radius: 4px;
  color: red;
}

.connect-btn {
  float: right;
  cursor: pointer;
  padding: 4px 10px;
  margin-top: 16px;
  border: 1px solid green;
  border-radius: 4px;
  color: green;
}

.notice-wrapper {
  width: 960px;
  margin: 80px auto 0;
  color: blueviolet;
  text-align: center;
  background: url('./assets/notice.png') 210px center / auto 24px no-repeat;
}

.notice-wrapper span {
  font-weight: bold;
}

/* trade panel */

.trade-container {
  position: relative;
  width: 960px;
  height: 640px;
  margin: 20px auto 0;
}

.make-wrapper {
  position: absolute;
  left: 20px;
  top: 0;
  width: 450px;
  height: 600px;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 0 6px rgba(0, 0, 0, .1);
}

.container-header {
  position: relative;
  font-size: 16px;
  font-weight: bold;
  padding: 12px 0 12px 20px;
  border-bottom: 1px solid #2c2c2c;
}

.form-item {
  margin-top: 40px;
  padding: 0 24px;
}

.item-title {
  font-size: 18px;
}

.item-title .hint {
  float: right;
  font-size: 12px;
  color: #888;
  padding-top: 4px;
}

.input {
  border: 0;
  border-bottom: 1px solid #2c2c2c;
  border-radius: 0;
  width: 100%;
  box-sizing: border-box;
  padding: 18px 60px 18px 0;
  outline: none;
}

.input.inline-input {
  flex: 1;
  padding-left: 12px;
}

.input:disabled {
  background-color: #fff;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

.input-wrapper {
  position: relative;
  display: flex;
}

.fee-rate {
  padding: 0 24px;
  color: #888;
}

.make-btn {
  position: absolute;
  bottom: 40px;
  right: 24px;
  left: 24px;
  line-height: 44px;
  text-align: center;
  border-radius: 4px;
  color: #2c2c2c;
  cursor: pointer;
  border: 1px solid #2c2c2c;
  background: #fff;
}

.trade-panel {
  display: flex;
  flex-direction: column;
  position: absolute;
  right: 20px;
  top: 0;
  width: 450px;
  height: 600px;
}

.panel-header {
  position: relative;
  font-size: 16px;
  font-weight: bold;
  padding: 12px 0;
  border-bottom: 1px solid #2c2c2c;
}

.panel-header .tabs {
  position: absolute;
  right: 0;
  top: 0;
  display: flex;
}

.tab-wrapper {
  flex: 1;
  margin-left: 20px;
  padding: 10px 0 10px 24px;
  cursor: pointer;
  background: url('./assets/radio.png') left center / 20px auto no-repeat;
}

.tab-wrapper.selected {
  background-image: url('./assets/radio_checked.png');
}

.list-title {
  display: flex;
}

.blank-placeholder {
  width: 32px;
}

.title-items {
  flex: 1;
  display: flex;
}

.item-text {
  flex: 1;
  text-align: right;
  color: #888;
  font-size: 12px;
  padding: 4px;
}

.item-placeholder {
  width: 58px;
}

.last-price-wrapper {
  display: flex;
  font-size: 16px;
  padding: 4px 0;
}

.last-price-label {
  width: 90px;
  padding: 0 2px;
  box-sizing: border-box;
}

.last-price {
  width: 151px;
  text-align: right;
  padding: 0 2px;
  box-sizing: border-box;
}

.last-price-placeholder {
  flex: 1;
}

.sell-section {
  position: relative;
  flex: 1;
  display: flex;
  color: red;
  text-align: center;
  align-items: center;
  border-bottom: 1px solid #e5e5e5;
  background: linear-gradient(to right, #ffffff, #f7f7f7);
}

.buy-section {
  position: relative;
  flex: 1;
  display: flex;
  color: green;
  text-align: center;
  align-items: center;
  border-bottom: 1px solid #e5e5e5;
  background: linear-gradient(to right, #f7f7f7, #ffffff);
}

.section-title {
  width: 32px;
}

.make-list {
  position: absolute;
  flex: 1;
  top: 0;
  right: 0;
  bottom: 0;
  left: 32px;
  overflow-y: auto;
  color: #2c2c2c;
}

.make-list.reverse {
  transform: scaleY(-1);
}

.my-make-item {
  display: flex;
}

.my-make-item:hover {
  background-color: rgba(0, 0, 0, .1);
}

.my-make-item.reverse {
  transform: scaleY(-1);
}

.make-item-detail {
  flex: 1;
  text-align: right;
  font-size: 14px;
  padding: 2px;
}

.unmake-btn {
  width: 48px;
  padding: 2px;
  margin-left: 10px;
  box-sizing: border-box;
  font-size: 14px;
  cursor: pointer;
  visibility: hidden;
}

.unmake-btn {
  color: red;
}

.my-make-item:hover .unmake-btn {
  visibility: visible;
}

.make-sell {
  color: red;
}

.make-buy {
  color: green;
}

.token-pool {
  width: 920px;
  margin: 0 auto;
}

.pool-item {
  display: flex;
  line-height: 36px;
}

.pool-item:nth-child(odd) {
  background: rgba(0, 0, 0, .03);
}

.pool-item:hover {
  background: rgba(0, 0, 0, .07);
}

.pool-item .token-name {
  flex: 1;
  padding: 0 0 0 20px;
}

.pool-item .token-balance {
  flex: 2;
  font-weight: bold;
  text-align: right;
  padding: 0 20px 0 0;
}

.option-wrapper {
  display: flex;
}

.option-icon {
  width: 24px;
  height: 24px;
  margin-right: 12px;
}

.option-icon-pWBTC {
  background: url('./assets/pWBTC.svg') center center / 100% auto no-repeat;
}

.option-icon-ONTd {
  background: url('./assets/ONTd.svg') center center / 100% auto no-repeat;
}

.option-icon-prenBTC {
  background: url('./assets/prenBTC.svg') center center / 100% auto no-repeat;
}

.option-icon-pUSDC {
  background: url('./assets/pUSDC.svg') center center / 100% auto no-repeat;
}

.option-icon-WING {
  background: url('./assets/wing_icon.svg') center center / 100% auto no-repeat;
}

.option-icon-pETH {
  background: url('./assets/ETH.svg') center center / 100% auto no-repeat;
}

.option-icon-pDAI {
  background: url('./assets/oDAI.svg') center center / 100% auto no-repeat;
}

.option-icon-pUSDT {
  background: url('./assets/pusdt.svg') center center / 100% auto no-repeat;
}

.option-icon-psUSD {
  background: url('./assets/psusd.svg') center center / 100% auto no-repeat;
}

.option-icon-pNEO {
  background: url('./assets/neo-ont.svg') center center / 100% auto no-repeat;
}

.option-icon-pUNI {
  background: url('./assets/UNI-ONT.svg') center center / 100% auto no-repeat;
}

.option-icon-ONG {
  background: url('./assets/ONG_icon.svg') center center / 100% auto no-repeat;
}

.option-icon-ONT {
  background: url('./assets/ONT.svg') center center / 100% auto no-repeat;
}

.option-icon-UNX {
  background: url('./assets/unx.png') center center / 100% auto no-repeat;
}

.option-label {
  flex: 1;
  padding: 2px 0;
}